/* Search Bar */
.search-bar {
    text-align: center;
    margin: 20px 0;
}

.search-bar input {
    width: 60%;
    padding: 10px 15px;
    font-size: 16px;
    border: 2px solid #9651ED;
    border-radius: 25px;
    outline: none;
    transition: box-shadow 0.3s ease-in-out;
}

.search-bar input:focus {
    box-shadow: 0 0 10px rgba(150, 81, 237, 0.5);
}

/* Product Grid */
.product-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    padding: 20px;
}

.product-card {
    border: 1px solid #ddd;
    border-radius: 10px;
    text-align: center;
    padding: 15px;
    background-color: #f9f9f9;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.product-card:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}

.product-card img {
    width: 100%;
    height: auto;
    max-height: 150px;
    object-fit: contain;
    margin-bottom: 10px;
}

.product-card h3 {
    font-size: 1.2em;
    color: #333;
    margin: 5px 0;
}

.product-card p {
    font-size: 0.9em;
    color: #666;
    margin: 5px 0;
}
