html, body {
    margin: 0;
    padding: 0;
    height: 100%; /* Ensure the full height of the viewport */
}

#root {
    display: flex;
    flex-direction: column;
    min-height: 100%; /* Ensure the content takes up at least the full height */
}

main {
    flex: 1; /* This makes the main content area expand to fill the available space */
}

footer {
    position: relative; /* Change to relative to allow proper placement */
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #9651ED;
    color: white;
    text-align: center;
    padding: 1rem;
    margin-top: auto; /* Push the footer to the bottom */
}

footer nav a {
    color: white;
    text-decoration: none;
    margin: 0 10px;
}

footer nav a:hover {
    text-decoration: underline;
}
