header {
    background: #9651ED;
    color: white;
    padding: 1rem;
    text-align: center;
}

header nav a {
    color: white;
    text-decoration: none;
    margin: 0 10px;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    transition: background-color 0.3s ease-in-out;
}

header nav a:hover {
    background-color: rebeccapurple;
}

header hr {
    border-top: 3px solid rebeccapurple;
    border-bottom: 1px solid #9651ED;
    width: 100%;
    margin: 15px 0;
}

/* Logo Styling */
.header-image {
    text-align: center;
    margin: 10px;
}

.header-image img {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    object-fit: cover;
}
