body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    line-height: 1.6;
}

.hero {
    text-align: center;
    padding: 2rem;
    background: #f4f4f4;
}

.hero h2 {
    color: #9651ED;
    margin-bottom: 1rem;
}

.btn {
    background: #9651ED;
    color: white;
    padding: 0.5rem 1rem;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;
}

.btn:hover {
    background-color: rebeccapurple;
}

.benefits {
    padding: 1rem;
    background: #e9e9e9;
}

.benefits h3 {
    color: #9651ED;
}

.benefits ul {
    list-style: disc;
    padding-left: 2rem;
}
