body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    line-height: 1.6;
    background: #f4f4f4; /* Added subtle background for overall page */
}

.profile {
    max-width: 800px;
    margin: 20px auto;
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    text-align: center; /* Centralizes content inside the section */
}

.profile img {
    display: block;
    margin: 0 auto; /* Centers the avatar */
    width: 150px; /* Set desired size */
    height: 150px;
    border-radius: 50%; /* Makes the image circular */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Adds a subtle shadow */
    object-fit: cover; /* Ensures the image fits well inside the circle */
}


.profile-info {
    text-align: center;
    margin-top: 20px;
}

.profile-info h2 {
    color: #9651ED;
    margin-bottom: 10px;
}

.profile-info p {
    color: #333;
    margin: 5px 0;
}

.user-activity {
    margin-top: 30px;
}

.user-activity h2 {
    text-align: center;
    color: #0078D7;
    margin-bottom: 20px;
}

.user-activity ul {
    list-style-type: none;
    padding: 0;
}

.user-activity li {
    margin-bottom: 10px;
    padding: 10px;
    background: #e9e9e9;
    border-radius: 5px;
}

.user-activity li strong {
    font-weight: bold;
}

