body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    line-height: 1.6;
}

.hero {
    text-align: center;
    padding: 2rem;
    background: #f4f4f4;
}

.hero h2 {
    margin-bottom: 1rem;
    color: #0078D7;
}

.btn {
    background: #0078D7;
    color: white;
    padding: 0.5rem 1rem;
    text-decoration: none;
    border-radius: 5px;
}

.benefits {
    padding: 1rem;
    background: #e9e9e9;
}


.contact-form {
    max-width: 400px;
    margin: 2rem auto;
    background: white;
    padding: 2rem;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.contact-form label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
}


.contact-form form {
    max-width: 400px;
    margin: 0 auto;
}

.contact-form textarea {
    resize: none;
    height: 150px;
    width: 100%;
    box-sizing: border-box;
}
.contact-form input {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
}

.contact-form button {
    background: #9651ED;
    color: white;
    padding: 0.5rem 1rem;
    text-decoration: none;
    border-radius: 5px;
}

